import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import ListBanners from '../components/ListBanners.vue';
import MessageBox from '../components/MessageBox.vue';
import msgBoxTools from '../mixins/msgBoxTools';
import sleep from '@/mixins/sleep';
import { bannersMapper } from '../module/store';
import ButtonApp from '@/components/ButtonApp.vue';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapState(['isLoading']),
        ...bannersMapper.mapGetters(['listSorted'])
    },
    methods: {
        ...bannersMapper.mapActions(['getList', 'deleteBanner'])
    }
});
let PageMain = class PageMain extends Mixins(msgBoxTools, Mappers) {
    constructor() {
        super(...arguments);
        this.deleteId = null;
        this.secondBtn = null;
    }
    created() {
        this.updateList();
    }
    // CLICK HANDLERS
    onCreateClick() { this.$router.push({ path: '/banners/create' }); }
    onDeleteClick(id) {
        // store id here in case of repeated request
        if (id)
            this.deleteId = id;
        this.secondBtn = { type: 'danger', isPlain: true };
        this.requestStatus = 'beforeDelete';
        this.openMsgBox();
    }
    onFirstBtnClick() {
        switch (this.requestStatus) {
            case 'beforeDelete':
                this.closeMsgBox();
                this.deleteItem();
            case 'failFetchList':
                this.updateList();
            default:
                this.closeMsgBox();
        }
    }
    // STORE ACTIONS CALL
    deleteItem() {
        this.deleteBanner(this.deleteId)
            .then(async () => {
            this.secondBtn = { type: 'success', isPlain: true };
            this.requestStatus = 'successDelete';
            this.openMsgBox();
            this.deleteId = null;
            this.updateList();
            await sleep(1500);
            this.closeMsgBox();
        })
            .catch(() => {
            this.secondBtn = { type: 'danger', isPlain: true };
            this.requestStatus = 'failDelete';
            this.updateList();
            this.openMsgBox();
        });
    }
    updateList() {
        this.$emit('updateList');
    }
};
PageMain = __decorate([
    Component({
        components: {
            ListBanners,
            MessageBox,
            ButtonApp
        },
        mixins: [
            msgBoxTools
        ]
    })
], PageMain);
export default PageMain;
