import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import MessageBox from '@/modules/banners/components/MessageBox.vue';
import { bannersMapper } from '@/modules/banners/module/store';
import MsgBoxTools from '@/modules/banners/mixins/msgBoxTools';
const Mappers = Vue.extend({
    methods: {
        ...bannersMapper.mapActions(['getList'])
    }
});
let ModuleBanners = class ModuleBanners extends Mixins(Mappers, MsgBoxTools) {
    created() {
        this.loadData();
    }
    loadData() {
        this.getList()
            .then(() => {
            console.log('List loaded!');
            this.closeMsgBox();
        })
            .catch(async (error) => {
            this.openMsgBox();
        });
    }
};
ModuleBanners = __decorate([
    Component({
        components: {
            MessageBox,
        }
    })
], ModuleBanners);
export default ModuleBanners;
